___CSS_LOADER_EXPORT___.push([module.id, `body.dark-theme,
.dark-theme .button,
.dark-theme .score-table,
.dark-theme .levels-window {
    background: #363636;
    color: #cccccc;
}

.dark-theme .levels-window__card {
    border-color: #a1a1a1;
}

.dark-theme .levels-window__card-level.Easy {
    color: #18a518;
}

.dark-theme .levels-window__card-level.Medium {
    color: #dfcc25;
}

.dark-theme .levels-window__card-level.Hard {
    color: #c75d4a;
}

.dark-theme .cell,
.dark-theme #space-fill {
    background: #cccccc;
    border: 1px solid #a1a1a1;
}

.dark-theme .button__slider {
    background: #b1b1b1;
}

.dark-theme #change-theme-btn:has(.switched) {
    background: #0f0f0f;
}

.dark-theme .cell.picked {
    background: #000000;
}

.dark-theme .crossed::before,
.dark-theme .crossed::after {
    background: #3b3b3b;
}

.dark-theme .clue {
    color: #3b3b3b;
}

.dark-theme #horizontal-clues {
    border-right: 3px solid rgb(54, 54, 54);
}

.dark-theme .app__game-field,
.dark-theme #vertical-clues-wrapper,
.dark-theme #horizontal-clues {
    background: rgb(54, 54, 54);
}

.dark-theme #vertical-clues-wrapper {
    border-bottom: 3px solid rgb(54, 54, 54);
}

.dark-theme .app__game-field,
.dark-theme #vertical-clues-wrapper,
.dark-theme #horizontal-clues {
    background: rgb(91 91 91);
}

@media (hover: hover) {
    .dark-theme .button:hover {
        background: rgb(97, 97, 97);
    }

    .dark-theme .button:disabled {
        background-color: #373737;
        color: #838181;
    }

    .dark-theme .button:disabled:hover {
        background-color: #373737;
    }

    .dark-theme .levels-window__card:has(.Easy):hover {
        background-color: #106d1050;
    }

    .dark-theme.levels-window__card:has(.Medium):hover {
        background-color: #9c8e1277;
    }

    .dark-theme .levels-window__card:has(.Hard):hover {
        background-color: #8f1d096c;
    }

    .dark-theme #choose-game-btn,
    .dark-theme #random-game-btn,
    .dark-theme #high-score-btn,
    .dark-theme #sound-control-btn,
    .dark-theme #sound-control-btn.sound-off,
    .dark-theme #save-game-btn,
    .dark-theme #continue-game-btn,
    .dark-theme #reset-game-btn,
    .dark-theme #show-solution-btn {
        filter: invert();
    }
}`, "",{"version":3,"sources":["webpack://./css/dark-theme.css"],"names":[],"mappings":"AAAA;;;;IAII,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,cAAc;AAClB;;AAEA;;IAEI,mBAAmB;IACnB,yBAAyB;AAC7B;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;;IAEI,mBAAmB;AACvB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,uCAAuC;AAC3C;;AAEA;;;IAGI,2BAA2B;AAC/B;;AAEA;IACI,wCAAwC;AAC5C;;AAEA;;;IAGI,yBAAyB;AAC7B;;AAEA;IACI;QACI,2BAA2B;IAC/B;;IAEA;QACI,yBAAyB;QACzB,cAAc;IAClB;;IAEA;QACI,yBAAyB;IAC7B;;IAEA;QACI,2BAA2B;IAC/B;;IAEA;QACI,2BAA2B;IAC/B;;IAEA;QACI,2BAA2B;IAC/B;;IAEA;;;;;;;;;QASI,gBAAgB;IACpB;AACJ","sourcesContent":["body.dark-theme,\r\n.dark-theme .button,\r\n.dark-theme .score-table,\r\n.dark-theme .levels-window {\r\n    background: #363636;\r\n    color: #cccccc;\r\n}\r\n\r\n.dark-theme .levels-window__card {\r\n    border-color: #a1a1a1;\r\n}\r\n\r\n.dark-theme .levels-window__card-level.Easy {\r\n    color: #18a518;\r\n}\r\n\r\n.dark-theme .levels-window__card-level.Medium {\r\n    color: #dfcc25;\r\n}\r\n\r\n.dark-theme .levels-window__card-level.Hard {\r\n    color: #c75d4a;\r\n}\r\n\r\n.dark-theme .cell,\r\n.dark-theme #space-fill {\r\n    background: #cccccc;\r\n    border: 1px solid #a1a1a1;\r\n}\r\n\r\n.dark-theme .button__slider {\r\n    background: #b1b1b1;\r\n}\r\n\r\n.dark-theme #change-theme-btn:has(.switched) {\r\n    background: #0f0f0f;\r\n}\r\n\r\n.dark-theme .cell.picked {\r\n    background: #000000;\r\n}\r\n\r\n.dark-theme .crossed::before,\r\n.dark-theme .crossed::after {\r\n    background: #3b3b3b;\r\n}\r\n\r\n.dark-theme .clue {\r\n    color: #3b3b3b;\r\n}\r\n\r\n.dark-theme #horizontal-clues {\r\n    border-right: 3px solid rgb(54, 54, 54);\r\n}\r\n\r\n.dark-theme .app__game-field,\r\n.dark-theme #vertical-clues-wrapper,\r\n.dark-theme #horizontal-clues {\r\n    background: rgb(54, 54, 54);\r\n}\r\n\r\n.dark-theme #vertical-clues-wrapper {\r\n    border-bottom: 3px solid rgb(5